export const getGrammageToUnit = (grammageUnit) => {
  return grammageUnit === 'Kilogramy'
    ? 'kg'
    : grammageUnit === 'Gramy'
    ? 'g'
    : grammageUnit === 'Kus'
    ? 'ks'
    : '';
};

export const getUnitToGrammage = (grammageUnit) => {
  return grammageUnit === 'kg'
    ? 'Kilogramy'
    : grammageUnit === 'g'
    ? 'Gramy'
    : grammageUnit === 'ks'
    ? 'Kus'
    : '';
};
