import React, { useState } from 'react';
import { Doughnut } from 'react-chartjs-2';
import Chip from '@material-ui/core/Chip';
import {
  MACRO_SECTION_DATA,
  VITAMINS_SECTION_DATA,
  MINERALS_SECTION_DATA,
} from '../constants/nutrients';
import { donutOptions, renderDonutData } from '../helpers/donutOptions';

const DONUT_BUTTON_DATA = [
  {
    title: 'Makro',
    mode: 'macro',
    dataType: MACRO_SECTION_DATA,
    labels: ['Bílkoviny', 'Sacharidy', 'Tuky'],
  },
  {
    title: 'Vitamíny',
    mode: 'vitamins',
    dataType: VITAMINS_SECTION_DATA,
    labels: ['B3', 'C', 'D', 'E', 'K'],
  },
  {
    title: 'Minerály',
    mode: 'minerals',
    dataType: MINERALS_SECTION_DATA,
    labels: ['Vápník', 'Draslík', 'Hořčík', 'Železo'],
  },
];

export const Donut = ({ getDonutData }) => {
  const [donutData, setDonutData] = useState({
    title: 'Makro',
    mode: 'macro',
    dataType: getDonutData(MACRO_SECTION_DATA, 'macro'),
    labels: ['Bílkoviny', 'Sacharidy', 'Tuky'],
  });

  return (
    <div className='donut'>
      <div className='donut__head'>
        {DONUT_BUTTON_DATA.map(({ title, mode, dataType, labels }) => (
          <Chip
            className='donut__chip'
            label={title}
            clickable
            color={`${donutData.title === title ? 'primary' : ''}`}
            onClick={() =>
              setDonutData({
                title,
                dataType: getDonutData(dataType, mode),
                labels,
              })
            }
          />
        ))}
      </div>
      <div className='donut__body'>
        <Doughnut
          data={renderDonutData(donutData.dataType, donutData.labels)}
          width={240}
          height={240}
          options={donutOptions}
        />
      </div>
    </div>
  );
};
