import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import {
  AuthProvider,
  ResponseStateProvider,
  FoodStateProvider,
  MenuStateProvider,
  RecipesProvider,
} from './context';
import { BrowserRouter as Router } from 'react-router-dom';

import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#43a047',
    },
    secondary: {
      main: '#ff5722',
    },
  },
});

ReactDOM.render(
  <React.StrictMode>
    <ResponseStateProvider>
      <AuthProvider>
        <RecipesProvider>
          <MenuStateProvider>
            <FoodStateProvider>
              <ThemeProvider theme={theme}>
                <Router>
                  <App />
                </Router>
              </ThemeProvider>
            </FoodStateProvider>
          </MenuStateProvider>
        </RecipesProvider>
      </AuthProvider>
    </ResponseStateProvider>
  </React.StrictMode>,
  document.getElementById('root')
);
