import React from 'react';
import { TextField, Chip } from '@material-ui/core';
import { SearchBarCheckbox } from './SearchBarCheckbox';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { useFoodStateValue } from '../context/FoodStateContext';
import { SEARCHBAR_FOOD_LIST } from '../constants/mock-food';

export const SelectBar = () => {
  const { foodState } = useFoodStateValue();

  const handleIncludedItems = (e, values) => {
    console.log(e.target.textContent);

    const loweredCamelTitle = e.target.textContent
      .replace(/\s/g, '-')
      .toLowerCase();

    const foodCardBody = document.querySelector(
      `.food-card--${loweredCamelTitle} .MuiCardActionArea-root`
    );

    foodCardBody.click();
  };

  return (
    <Autocomplete
      onChange={handleIncludedItems}
      multiple
      id='checkboxes-tags-demo'
      options={SEARCHBAR_FOOD_LIST}
      disableCloseOnSelect
      getOptionLabel={(option) => option.title}
      renderTags={(value, getTagProps) => {
        value.map(({ title, index }) => (
          <Chip
            variant='outlined'
            key={index}
            label={title}
            onDelete={() => console.log('test')}
          />
        ));
      }}
      renderOption={(option, { selected }) => {
        const isIncludedInState = foodState.find(
          (foodItem) => foodItem.title === option.title
        );

        isIncludedInState ? (selected = true) : (selected = false);

        return <SearchBarCheckbox option={option} selected={selected} />;
      }}
      style={{ width: '80%' }}
      renderInput={(params) => (
        <TextField
          {...params}
          variant='outlined'
          label='Vyhledej si mě'
          placeholder='Např. Pomeranč'
        />
      )}
    />
  );
};
