import React, { createContext, useContext, useState, useEffect } from 'react';

export const ResponseStateContext = createContext();

export const ResponseStateProvider = ({ children }) => {
  const [response, setResponse] = useState({
    active: false,
    message: '',
    type: '',
  });
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    response.active &&
      setTimeout(() => {
        setResponse({ ...response, active: false });
      }, 4000);
  }, [response]);

  return (
    <ResponseStateContext.Provider
      value={{ response, setResponse, isLoading, setIsLoading }}
    >
      {children}
    </ResponseStateContext.Provider>
  );
};

export const useResponseStateValue = () => useContext(ResponseStateContext);
