import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  TextField,
} from '@material-ui/core';
import { Fabs } from '../compontents/Fabs';
import { useFoodStateValue } from '../context/FoodStateContext';
import { useResponseStateValue } from '../context/ResponseStateContext';
import { useAuthValue } from '../context/AuthContext';
import { useRecipesValue } from '../context/RecipesContext';
import { firebase } from '../firebase';

const useStyles = makeStyles((theme) => ({
  width: {
    minWidth: '250px',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
}));

export function CreateRecipeModal({ fab, value, index }) {
  const [open, setOpen] = useState(false);
  const [inputText, setInputText] = useState('');
  const { foodState, setFoodState } = useFoodStateValue();
  const { recipes, setRecipes } = useRecipesValue();
  const { currentUser } = useAuthValue();
  const { setResponse, isLoading, setIsLoading } = useResponseStateValue();
  const classes = useStyles();

  const createRecipe = async () => {
    setOpen(false);
    setIsLoading(true);
    try {
      await firebase.firestore().collection('recipes').add({
        name: inputText,
        itemsList: foodState,
        userId: currentUser.uid,
      });

      setRecipes([...recipes]);
      setFoodState([]);
      setResponse({
        active: true,
        message: 'Recept byl úspěšně vytvořen',
        type: 'success',
      });
    } catch (error) {
      setResponse({
        active: true,
        message: error.message,
        type: 'error',
      });
    }
    setIsLoading(false);
  };

  return (
    <div>
      <Fabs
        fab={fab}
        value={value}
        index={index}
        setOpen={setOpen}
        isLoading={isLoading}
      />
      <Dialog
        disableBackdropClick
        disableEscapeKeyDown
        open={open}
        onClose={() => setOpen(false)}
      >
        <DialogTitle>Vytvořit Recept</DialogTitle>
        <DialogContent className={classes.width}>
          <TextField
            onInput={(e) => setInputText(e.target.value)}
            autoFocus
            margin='dense'
            id='name'
            label='Název Receptu'
            type='email'
            fullWidth
          />
        </DialogContent>
        <DialogActions>
          <Button
            disabled={isLoading}
            onClick={() => setOpen(false)}
            color='primary'
          >
            Cancel
          </Button>
          <Button
            disabled={inputText === '' || isLoading}
            onClick={createRecipe}
            color='primary'
          >
            Ok
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
