import React from 'react';
import {
  ListItem,
  ListItemAvatar,
  ListItemText,
  ListItemSecondaryAction,
  IconButton,
  Avatar,
} from '@material-ui/core';
import { useFoodStateHelpers } from '../hooks/useFoodStateHelpers';
import { useMenuStateValue } from '../context/MenuStateContext';

import DeleteIcon from '@material-ui/icons/Delete';
import FastfoodIcon from '@material-ui/icons/Fastfood';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  listItem: {
    paddingLeft: 0,
    paddingRight: 0,
  },
  iconButton: {
    padding: 0,
    height: 48,
    width: 48,
  },
}));

export const MenuItemList = ({ recipe }) => {
  const classes = useStyles();
  const { calculateFoodValue } = useFoodStateHelpers();
  const { menuState, setMenuState } = useMenuStateValue();

  const { name, itemsList, id } = recipe;

  const removeMenuListItem = () => {
    const newMenuState = menuState.filter((recipe) => recipe.id !== id);
    setMenuState(newMenuState);
  };

  // console.log('menuState', menuState);
  // console.log('recipe id', id);

  return (
    <div className='menu__item'>
      <ListItem className={classes.listItem}>
        <ListItemAvatar>
          <Avatar>
            <FastfoodIcon />
          </Avatar>
        </ListItemAvatar>
        <ListItemText
          primary={name}
          secondary={`${calculateFoodValue(
            itemsList,
            'calories',
            null,
            false
          )} kcal`}
        />
        <ListItemSecondaryAction>
          <IconButton
            onClick={removeMenuListItem}
            className={classes.iconButton}
            edge='end'
            aria-label='delete'
          >
            <DeleteIcon />
          </IconButton>
        </ListItemSecondaryAction>
      </ListItem>
    </div>
  );
};
