import React, { useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { Container, Button, TextField } from '@material-ui/core';
import { LottieAnimation, PasswordInput } from '../../compontents';
import { useAuthValue } from '../../context/AuthContext';
import { useResponseStateValue } from '../../context/ResponseStateContext';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  margin: {
    margin: theme.spacing(1),
  },

  textField: {
    width: '100%',
    margin: 0,
    marginBottom: '1.5rem',
  },
  link: {
    color: '#43a047',
  },
  text: {
    color: '#858585',
    marginTop: '1rem',
  },
}));

export const LoginPage = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const { setResponse, isLoading, setIsLoading } = useResponseStateValue();
  const { login } = useAuthValue();
  const history = useHistory();
  const classes = useStyles();

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      setIsLoading(true);
      await login(email, password);
      history.push('/');
    } catch (error) {
      console.log(error);
      setResponse({ active: true, message: error.message, type: 'error' });
    }

    setIsLoading(false);
  };

  return (
    <Container className='auth auth--login'>
      <div className='auth__header'>
        <LottieAnimation url='./images/animations/cooking.json' />
        <h1 className='auth__title'>Vítejte zpátky</h1>
      </div>
      <div className='auth__body'>
        <form id='login' onSubmit={handleSubmit}>
          <TextField
            onChange={(e) => setEmail(e.target.value)}
            className={classes.textField}
            id='outlined-basic'
            label='E-mail'
            variant='outlined'
          />
          <PasswordInput setValue={setPassword} text='Heslo' />
          <Button
            disabled={isLoading}
            type='submit'
            className='auth__login'
            variant='contained'
            color='primary'
          >
            Přihlásit se
          </Button>
        </form>
      </div>
      <div className='auth__footer'>
        <Button disabled={isLoading} variant='outlined' color='primary'>
          Přihlásit se přes Google
        </Button>
        <p className={classes.text}>
          Nemáte ještě účet?{' '}
          <Link
            className={classes.link}
            disabled={isLoading}
            to='/register'
            color='primary'
          >
            Zaregistrujte se zde!
          </Link>
        </p>
      </div>
    </Container>
  );
};
