import React from 'react';
import { useResponseStateValue } from '../context/ResponseStateContext';

export const AlertBanner = ({ error }) => {
  const { response } = useResponseStateValue();

  return (
    <div
      className={`alert-banner alert-banner--${response.type} ${
        response.active ? 'alert-banner--show' : ''
      }`}
    >
      {response.message}
    </div>
  );
};
