import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Modal, Button } from '@material-ui/core';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';

const useStyles = makeStyles((theme) => ({
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    outline: 'none',
    borderRadius: 4,
    boxShadow:
      '0px 11px 15px -7px rgb(0 0 0 / 20%), 0px 24px 38px 3px rgb(0 0 0 / 14%), 0px 9px 46px 8px rgb(0 0 0 / 12%)',
    padding: theme.spacing(2, 4),
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
  },
}));

export const MenuItemsListModal = ({ children, modalStyle }) => {
  const classes = useStyles();
  const [open, setOpen] = useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const childrenWithProps = children.map((child) => {
    // checking isValidElement is the safe way and avoids a typescript error too
    if (React.isValidElement(child)) {
      return React.cloneElement(child, { isOpen: open });
    }
    return child;
  });

  return (
    //! Unifiy all modal windows => one modal and pass content as {child} prop
    <div>
      <div onClick={handleOpen}>{childrenWithProps[0]}</div>
      <Modal
        aria-labelledby='transition-modal-title'
        aria-describedby='transition-modal-description'
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={`modal ${classes.paper} ${modalStyle}`}>
            <div className='modal__body'>{childrenWithProps[1]}</div>
            <div className='modal__footer'>
              <Button onClick={() => setOpen(false)} color='primary'>
                Cancel
              </Button>
            </div>
          </div>
        </Fade>
      </Modal>
    </div>
  );
};
