import React from 'react';
import { AppBar, Toolbar } from '@material-ui/core';
import { Link } from 'react-router-dom';
import { MenuBasket } from '../MenuBasket';
import cholifyLogo from '../../img/cholify-inline-logo-dark.svg';
import { MenuItemsListModal } from '../MenuItemsListModal';

import { MenuItemList } from '../MenuItemList';
import { useMenuStateValue } from '../../context/MenuStateContext';
import { List } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  listItem: {
    paddingLeft: 0,
    paddingRight: 0,
  },
  modalStyle: {
    minWidth: '75%',
    minHeight: '26rem',
  },
}));

export const Header = ({ passRef }) => {
  const classes = useStyles();
  const { menuState } = useMenuStateValue();

  console.log(menuState);

  return (
    <AppBar ref={passRef} className='header' position='absolute'>
      <Toolbar className='header__toolbar'>
        <Link to='/my-profile'>
          <img
            className='header__logo'
            src={cholifyLogo}
            alt='Cholify header logo'
          />
        </Link>
        <MenuItemsListModal modalStyle={classes.modalStyle}>
          {
            //? Button
            <MenuBasket menuItemCount={menuState.length} />
          }
          {
            //? Content
            <div className='menu-list'>
              <div className='menu-list__head'>
                <h2>Moje Menu</h2>
              </div>
              <div className='menu-list__body'>
                <List className={classes.root}>
                  {menuState.map((recipe) => (
                    <MenuItemList recipe={recipe} />
                  ))}
                </List>
              </div>
            </div>
          }
        </MenuItemsListModal>
      </Toolbar>
    </AppBar>
  );
};
