import React from 'react';
import { Container } from '@material-ui/core';
import { Donut } from '../compontents/Donut';
import { MacroDataSection, MicroDataSection } from '../compontents';
import { useMenuItemsList } from '../hooks/useMenuItemsList';
import { useMenuStateValue } from '../context/MenuStateContext';
import {
  MACRO_SECTION_DATA,
  VITAMINS_SECTION_DATA,
  MINERALS_SECTION_DATA,
} from '../constants/nutrients';

export const MenuNutrition = () => {
  const { calculateMenuItemsValue } = useMenuItemsList();
  const { menuState } = useMenuStateValue();

  const getDonutData = (nutritionType, mode) => {
    return nutritionType.map(({ name }) =>
      calculateMenuItemsValue(menuState, name, mode) == 0
        ? 0.1
        : calculateMenuItemsValue(menuState, name, mode)
    );
  };

  return (
    <Container className='recipe-nutrition'>
      <Donut getDonutData={getDonutData} />
      <div className='recipe-nutrition__data'>
        <div className='data-section'>
          <h3 className='data-section__title '>Kalorie</h3>
          <div className='data-section__row'>
            <p className='data-section__data'>
              Celkem:
              <span className='data-section__value'>
                {calculateMenuItemsValue(menuState, 'calories', null, false)}{' '}
                kcal
              </span>
            </p>
          </div>
        </div>
        {
          //! Macro
          MACRO_SECTION_DATA.map(({ title, name, subSection }) => (
            <MacroDataSection
              key={title}
              title={title}
              name={name}
              subSection={subSection}
              calculateValue={calculateMenuItemsValue}
              functionState={menuState}
            />
          ))
        }

        <MicroDataSection
          sectionType={VITAMINS_SECTION_DATA}
          sectionTitle='Vitamíny'
          nutrientType='vitamins'
          calculateValue={calculateMenuItemsValue}
          functionState={menuState}
        />

        <MicroDataSection
          sectionType={MINERALS_SECTION_DATA}
          sectionTitle='Minerály'
          nutrientType='minerals'
          calculateValue={calculateMenuItemsValue}
          functionState={menuState}
        />
      </div>
    </Container>
  );
};
